.header {
  position: sticky;
  top: 0;
  display: flex;
  background: #fff;
  justify-content: space-evenly;
  border-bottom: 1px solid #ccc;
  padding: 10px 0;
  width: 100%;
  z-index: 999;
}
.header__left {
  display: flex;
}
.header__left > img {
  object-fit: contain;
  height: 40px;
  margin-right: 10px;
}
.header__search {
  padding: 10px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  height: 22px;
  color: #6f6f6f;
  background-color: #eef3f8;
}
.header__search > input {
  outline: none;
  border: none;
  background: none;
}
.header__right {
  display: flex;
}
